<template>
  <div class="Search">
    <PlayBar v-if="currentSong"></PlayBar>
    <HomeLink></HomeLink>
    <div class="search-bar">
      <i class="fa fa-search"></i>
      <input type="text" name id v-model="searchValue" />
      <i class="fa fa-times" @click="UPDATE({key: 'searchValue', value: null, name: 'search'})"></i>
      <!-- 配合实时搜索 -->
    </div>
    <div class="search-suggestions">
      <div v-if="musicSearch.length !== 0">
        <div class="item-title">音乐相关</div>
        <div
          v-for="(item, index) in musicSearch"
          :key="index"
          @click="musicSearchCickHandle(item)"
          class="suggetion-item"
        >{{ item.name }}-{{ item.artists[0].name }}</div>
      </div>
    </div>
    <div class="item-title">搜索历史</div>
    <div class="searchHistory" v-if="searchHistory">
      <span
        v-for="(item, index) in searchHistory"
        :key="index"
        @click="UPDATE({key: 'searchValue', value: item.name, name: 'search'})"
      >{{ item.name }}-{{ item.artists[0].name }}</span>
      <!-- 更新搜索关键字 -->
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import types from "../store/modules/types";

import HomeLink from "../components/HomeLink.vue";
import PlayBar from "../components/PlayBar.vue";
export default {
  naem: "Search",
  components: { PlayBar, HomeLink },
  computed: {
    ...mapState(["err", "currentSong", "currentSongList"]),
    ...mapState("search", ["searchHistory", "musicSearch"]),
    searchValue: {
      get() {
        return this.$store.state.search.searchValue;
      },
      set(v) {
        this.UPDATE({ key: "searchValue", value: v, name: "search" });
      }
    }
    // vuex state.searchValue实现双向绑定
  },
  watch: {
    searchValue: function(newValue) {
      if (newValue) {
        setTimeout(() => {
          // 利用计时器缓存 newValue

          if (newValue === this.searchValue) {
            // 如果 缓存值 === 最新值

            this.axios
              .get("/search/suggest", {
                params: {
                  keywords: newValue
                }
              })
              .then(res => {
                if (res.data.result.songs) {
                  this.UPDATE({
                    key: "musicSearch",
                    value: res.data.result.songs.filter(i => i.id > 1000000000),
                    name: "search"
                  });
                } else {
                  this.UPDATE({
                    key: "musicSearch",
                    value: [],
                    name: "search"
                  });
                }
                // 更新musicSearch
              })
              .catch(err => {
                this.actionUPDATE({ key: "err", value: err });
              });
          }
        }, 1000);
      } else {
        this.UPDATE({
          key: "musicSearch",
          value: [],
          name: "search"
        });
        // 更新musicSearch
      }
    }
  },
  methods: {
    ...mapMutations([`${types.UPDATE}`]),
    ...mapActions({
      [`action${types.UPDATE}`]: `${types.UPDATE}`
    }),

    updateSearchData: function() {},
    // 实习搜索建议

    musicSearchCickHandle: function(item) {
      if (this.searchHistory.length !== 0) {
        let arr = this.searchHistory.filter(i => i.id !== item.id);
        // 去重

        arr.push(item);
        this.UPDATE({ key: "searchHistory", value: arr, name: "search" });
        // 更新searchHistory
      } else {
        this.UPDATE({ key: "searchHistory", value: [item], name: "search" });
        // 更新searchHistory
      }

      this.UPDATE({
        key: "currentSongListIndex",
        value: -1
      });
      // 更新当前播放歌曲列表下标

      this.UPDATE({ key: "currentSong", value: item });
      // 更新 currentSong

      let currentSongList = this.currentSongList.filter(i => {
        if (i) {
          return i.id !== item.id;
        } else {
          return false;
        }
      });
      // 去重

      this.UPDATE({
        key: "currentSongList",
        value: [...currentSongList, this.currentSong]
      });
      // 更新前播放列表
    }
    // 音乐相关click反馈
  }
};
</script>

<style lang="less" scoped>
.Search {
  padding-top: 38px;
  color: @c-b;
  .search-bar {
    // padding: 15px 10px;
    margin: 15px 10px;
    // height: 30px;
    background: #ebecec;
    border-radius: 15px;
    border-bottom: 1px solid #fbfcfd;
    display: flex;
    i {
      margin: 0 8px;
      width: 14px;
      height: 30px;
      overflow: hidden;
      line-height: 30px;
    }
    input {
      height: 30px;
      width: calc(100% - 60px);
      vertical-align: middle;
      font-size: 14px;
      color: @c-b;
      background: #ebecec;
      outline: none;
      border: none;
    }
  }
  .item-title {
    border-top: 1px solid #f7f1f1;
    font-size: 12px;
    color: @c-b;
    padding: 15px 10px;
  }
  .suggetion-item {
    cursor: pointer;
    font-size: 12px;
    color: @c-g;
    margin-bottom: 14px;
    margin-left: 15px;
    i {
      margin: 8px;
    }
  }
  .searchHistory {
    display: flex;
    flex-wrap: wrap;
    span {
      cursor: pointer;
      padding: 5px;
      border-radius: 8px;
      margin-left: 5px;
      margin-bottom: 14px;
      background: #fbfcfd;
      border: 1px solid #e2e2e2;
    }
  }
}
</style>
